<template>
  <div id="brand-table-component">
    <el-table
      :data="data"
      :style="{ width: '100%' }"
      :max-height="maxHeight"
      header-row-class-name="table-header"
      cell-class-name="table-cell"
    >
      <el-table-column width="250px" prop="id" label="Username">
        <template #default="scope">
          <a
            class="username-text"
            href="#"
            @click="onUsernameClick(scope.row.id)"
          >
            <p>{{ scope.row.username }}</p>
          </a>
        </template>
      </el-table-column>
      <el-table-column prop="nickname" label="Nickname" width="250px"> </el-table-column>
      <el-table-column prop="email" label="Email" width="200px"> </el-table-column>
      <el-table-column prop="company" label="Company" width="200px"> </el-table-column>
      <el-table-column prop="role_name" label="Role" width="200px"> </el-table-column>
      <el-table-column prop="phone" label="Phone" width="200px"> </el-table-column>
      <el-table-column prop="active" label="Status" width="200px">
        <template #default="scope">
          <div
            class="status-button"
            :class="getStatusButtonStyles(scope.row.active)"
          >
            {{ getStatusText(scope.row.active) }}
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { useRouter } from "vue-router";
import { Member } from "@/typings/member";

const getStatusButtonStyles = (active: boolean) => {
  return active ? "activated" : "expired";
};

const getStatusText = (active: boolean) => {
  return active ? "Activated" : "Expired";
};

const Component = defineComponent({
  name: "BrandTable",
  props: {
    data: {
      type: Array as PropType<Member[]>,
      default: () => []
    },
    maxHeight: {
      type: String,
      default: "auto"
    }
  },
  setup() {
    const router = useRouter();

    const onUsernameClick = async (id: number) => {
      await router.push({ name: "UserProfile", params: { id } });
    };

    return {
      onUsernameClick,

      getStatusText,
      getStatusButtonStyles
    };
  }
});

export default Component;
</script>

