    height 36px
  .activated
    background: $color-active
  .expired
    background: #606060

  :deep() .el-table
    background $color-background

  :deep() .el-table__header-wrapper
    border-bottom 1px solid $color-black
    thead
      color $color-black

  :deep() .table-header
    th
