const createCover = () => {
  const coverImage = process.env.VUE_APP_INVITE_SUCCESS_COVER_URL;
  return `
  <table
    align="center"
    border="0"
    cellpadding="0"
    cellspacing="0"
    width="100%"
  >
    <tr>
      <td style="font-size: 0; line-height: 0;" height="4">
        &nbsp;
      </td>
    </tr>
    <tr>
      <td width="592" valign="top" style="padding-right: 0; padding-left: 0;">
        <img
          src="${coverImage}"
          alt=""
          width="600"
          height="337"
          style="display: block;"
        />
      </td>
    </tr>
  </table>
  `;
};

