<template>
  <div id="search-bar-component">
    <el-input :placeholder="placeholder" :model-value="value" @input="onChange">
      <template #prefix>
        <font-awesome-icon
          style="color: #444444; font-size: 18px;"
          :icon="['fas', 'search']"
        ></font-awesome-icon>
      </template>
    </el-input>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

const Component = defineComponent({
  name: "SearchBar",
  props: {
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    }
  },
  setup(_, { emit }) {
    const onChange = (value: string) => {
      emit("change", value);
    };
    return {
      onChange
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#search-bar-component
  .el-input
    width 300px

    :deep() .el-input__prefix
      flex()
  p
    font-weight 400
    font-size $font-size-large
    margin-right 15px
</style>
