<template>
  <div id="colleagues-component">
    <div class="console-wrapper">
      <el-button
        v-permission="'member_colleague_add'"
        type="success"
        @click="onAddColleagueClick"
      >
        Add Colleague
      </el-button>
      <SearchBar
        placeholder="Search colleague..."
        :value="searchValue"
        @change.self="onSearchChange"
      ></SearchBar>
    </div>
    <div class="table-wrapper">
      <ColleaguesTable :data="list"></ColleaguesTable>
    </div>
    <ColleaguesAddDialog ref="colleaguesAddDialog" @load="onLoad"></ColleaguesAddDialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from "vue";
import ColleaguesTable from "./ColleaguesTable.vue";
import ColleaguesAddDialog from "./ColleaguesAddDialog.vue";
import SearchBar from "@/components/SearchBar.vue";
import { iCreateDialog } from "@/typings/component";
import { getMembers } from "@/apis/member";
import { Member } from "@/typings/member";
import { loading } from "@/utils";

const Component = defineComponent({
  name: "Colleagues",
  components: {
    ColleaguesTable,
    ColleaguesAddDialog,
    SearchBar
  },
  setup() {
    const colleaguesAddDialog = ref<iCreateDialog | null>(null);
    const searchValue = ref("");
    const list = ref<Member[]>([]);

    const init = async () => {
      const loadingInstance = loading();
      try {
        const { data } = await getMembers("colleagues");
        list.value = data;
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(init);

    const onAddColleagueClick = () => {
      if (!colleaguesAddDialog.value) return;
      colleaguesAddDialog.value.open();
    };

    const onSearchChange = async (value: string) => {
      searchValue.value = value;
      const { data } = await getMembers("assignable", searchValue.value);
      list.value = data;
    };

    const onLoad = async () => {
      await init()
    };

    return {
      list,
      colleaguesAddDialog,
      searchValue,

      onAddColleagueClick,
      onSearchChange,
      onLoad
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#colleagues-component
  page-container()
  .console-wrapper
    flex(space-between)
  .table-wrapper
    padding 30px 0
</style>
