
import { defineComponent } from "vue";

const Component = defineComponent({
  name: "SearchBar",
  props: {
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    }
  },
  setup(_, { emit }) {
    const onChange = (value: string) => {
      emit("change", value);
    };
    return {
      onChange
    };
  }
});

export default Component;
